#root {
  height: 100vh;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  background-color: #f5f5f5;
}

#quote-box {
  width: 350px;
  height: 250px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  background-color: white;
  border: 1px solid lightgray;
  border-radius: 5px;
  padding: 5px;
}

button {
  width: 90px;
  margin-left: auto;
}

#quote-box>div {
  text-align: right;
  margin-bottom: 25px;
}

#quote-box a {
  margin-top: auto;
  padding: 15px;
}

blockquote {
  height: 80px;
  align-content: center;
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
}